
.modal {
    position: fixed;
    left: 0;
    top: 0;

    width: 100%;
    height: 100%;
    
    overflow: auto;
    
    background-color: rgb(0,0,0);
    background-color: rgba(0,0,0,0.3);

    z-index: 1;
}

.modal__content {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 100%;
    height: 100%;
}